import { findIndex } from 'lodash-es';
/* 常用尺寸功能 */
const ID = '__MEDIA_CROP_MODAL__';
/**
 * 获取常用尺寸
 * @param { string } id
 */
export function getCommonSize(id) {
    const value = localStorage.getItem(id || ID);
    const result = value ? JSON.parse(value) : []; // TODO: 兼容代码
    return Array.isArray(result) ? result : result.commonSize;
}
/**
 * 设置常用尺寸
 * @param { SizeOptions } value
 * @param { string } id
 */
export function setCommonSize(value, id) {
    const sizeArr = getCommonSize(id);
    const index = findIndex(sizeArr, value);
    if (index < 0) {
        sizeArr.push(value);
        localStorage.setItem(id || ID, JSON.stringify({ commonSize: sizeArr }));
    }
}
/**
 * 删除常用尺寸
 * @param { SizeOptions } value
 * @param { string } id
 */
export function deleteCommonSize(value, id) {
    const sizeArr = getCommonSize(id);
    const index = findIndex(sizeArr, value);
    if (index >= 0) {
        sizeArr.splice(index, 1);
        localStorage.setItem(id || ID, JSON.stringify({ commonSize: sizeArr }));
    }
}
