import { createAction } from '@reduxjs/toolkit';
export const setCrop = createAction('设置裁剪尺寸');
export const setCallbackCrop = createAction('设置裁剪尺寸-callbackCrop');
export const setMediaPreview = createAction('设置图片预览的base64');
export const resetMediaInfo = createAction('重置media信息');
export const setMediaInfo = createAction('设置media信息');
export const clearMediaInfo = createAction('清除media信息');
export const setLoadImgLoading = createAction('设置加载图片状态');
export const setCropStyle = createAction('设置裁剪图片容器的宽高');
export const setMediaSrc = createAction('设置图片原始地址');
export const setStateCommonSize = createAction('设置常用尺寸');
export const setSizeOptionsValue = createAction('设置自定义尺寸');
export const setCommonSizeSelectValue = createAction('设置常用尺寸-commonSizeSelectValue');
export const setSize = createAction('设置尺寸');
export const setCustomSize = createAction('设置自定义尺寸');
export const setCustomWidth = createAction('设置自定义的宽');
export const setCustomHeight = createAction('设置自定义的高');
export const setVideoPlaying = createAction('设置播放状态');
export const setMetadata = createAction('设置视频信息');
export const setVideoCurrentTime = createAction('设置视频播放时间');
export const setVideoTime = createAction('设置视频时间');
export const setVideoLeftTime = createAction('设置视频的开始时间');
export const setVideoRightTime = createAction('设置视频的结束时间');
const actions = {
    [setCrop](state, action) {
        state.crop = action.payload;
    },
    [setCallbackCrop](state, action) {
        state.callbackCrop = action.payload;
    },
    [setMediaPreview](state, action) {
        // 当参数为数组时，修改预览的base64和安全裁剪预览的base64
        // 当参数为字符串时，只修改预览的base64
        if (Array.isArray(action.payload)) {
            state.mediaPreview = action.payload[0];
            state.safeMediaPreview = action.payload[1];
        }
        else {
            state.mediaPreview = action.payload;
            state.safeMediaPreview = undefined;
        }
    },
    [resetMediaInfo](state, action) {
        state.mediaJimp = undefined;
        state.mediaPreview = undefined;
    },
    [setMediaInfo](state, action) {
        state.mediaJimp = action.payload;
    },
    [clearMediaInfo](state, action) {
        state.mediaJimp = undefined;
        state.mediaSrc = undefined;
        state.mediaPreview = undefined;
        state.safeMediaPreview = undefined;
    },
    [setLoadImgLoading](state, action) {
        state.loadImgLoading = action.payload;
    },
    [setCropStyle](state, action) {
        state.cropStyle = action.payload;
    },
    [setMediaSrc](state, action) {
        state.mediaSrc = action.payload;
    },
    [setStateCommonSize](state, action) {
        state.commonSize = action.payload;
    },
    [setSizeOptionsValue](state, action) {
        state.sizeOptionsValue = action.payload;
    },
    [setCommonSizeSelectValue](state, action) {
        state.sizeOptionsValue = action.payload.sizeOptionsValue;
        state.customWidth = action.payload.customWidth;
        state.customHeight = action.payload.customHeight;
    },
    [setSize](state, action) {
        // eslint-disable-next-line @typescript-eslint/typedef
        [state.customWidth, state.customHeight] = action.payload;
        state.sizeOptionsValue = `${Number(action.payload[0])} * ${Number(action.payload[1])}`;
    },
    [setCustomSize](state, action) {
        // eslint-disable-next-line @typescript-eslint/typedef
        [state.customWidth, state.customHeight] = action.payload;
        state.sizeOptionsValue = '自定义尺寸';
    },
    [setCustomWidth](state, action) {
        state.customWidth = action.payload;
    },
    [setCustomHeight](state, action) {
        state.customHeight = action.payload;
    },
    [setVideoPlaying](state, action) {
        state.videoPlaying = action.payload;
    },
    [setMetadata](state, action) {
        state.videoCurrentTime = 0;
        state.videoDuration = action.payload.duration;
        state.videoLeftTime = 0;
        state.videoRightTime = action.payload.duration;
    },
    [setVideoCurrentTime](state, action) {
        state.videoCurrentTime = action.payload;
    },
    [setVideoTime](state, action) {
        // eslint-disable-next-line @typescript-eslint/typedef
        [state.videoLeftTime, state.videoRightTime] = action.payload;
    },
    [setVideoLeftTime](state, action) {
        state.videoLeftTime = action.payload;
    },
    [setVideoRightTime](state, action) {
        state.videoRightTime = action.payload;
    }
};
export default actions;
