/**
 * 替换图片或视频地址的协议头
 * @param { string } src: 图片或视频地址
 */
export function formatImageSrc(src) {
    if (/^http:/i.test(src)) {
        return src.replace(/^http:/i, '');
    }
    return src;
}
/**
 * 将秒格式化成HH:mm:ss.ms格式
 * @param { number } time: 秒的时间
 */
export function formatTime(time) {
    // 秒
    const sLen = Math.floor(time);
    const s = sLen % 60;
    // 分
    const mLen = Math.floor(sLen / 60);
    const m = mLen % 60;
    // 小时
    const hLen = Math.floor(mLen / 60);
    const h = hLen % 60;
    const sStr = String(s).padStart(2, '0');
    const mStr = String(m).padStart(2, '0');
    const hStr = String(h).padStart(2, '0');
    const str = `${hStr}:${mStr}:${sStr}`;
    return str;
}
/* 根据选项，生成安全线尺寸 */
export function getSafeSize(sizeOptions = []) {
    const result = {};
    for (const item of sizeOptions) {
        const key = `${item.width} * ${item.height}`;
        if (item.safeWidth) {
            result[key] = { width: item.safeWidth };
        }
        if (item.safeHeight) {
            result[key] = { height: item.safeHeight };
        }
    }
    return result;
}
/**
 * 读取本地图片
 * @param { File } file: 上传文件的对象
 */
export function readFile(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.addEventListener('load', function (event) {
            resolve(this.result);
        });
        reader.readAsDataURL(file); // 图片转换成base64
    });
}
const PREFIX_NAME = 'rc-media-crop-modal-';
/**
 * 返回一个类名
 * @param { string } className: 类名
 */
export function styleName(className) {
    return `${PREFIX_NAME}${className}`;
}
/**
 * 将sizeOptionsValue解析成宽高
 * @param { SizeOptionsValue | string } sizeOptionsValue: 宽高
 */
export function formatSizeOptions(sizeOptionsValue) {
    if (sizeOptionsValue === '自定义尺寸') {
        throw new Error(`无法解析：${sizeOptionsValue}`);
    }
    const [width, height, text] = sizeOptionsValue.split(/\s*\*\s*/);
    return [Number(width), Number(height), text];
}
/* 解析图片预览的容器宽高 cropStyle */
export function formatCropStyle(cropStyle) {
    const cropWidth = Number(cropStyle.width.match(/[0-9]*/));
    const cropHeight = Number(cropStyle.height.match(/[0-9]*/));
    return [cropWidth, cropHeight];
}
