const canvas = document.createElement('canvas');
const ctx = canvas.getContext('2d');
/**
 * 图片转换成base64
 * @param { HTMLImageElement } image: 图片的dom
 * @param { string } fileType: 图片类型
 * @param { DrawOptions } drawOptions
 */
function imageToBase64(image, fileType, drawOptions) {
    const { startX, startY, startWidth, startHeight, drawWidth, drawHeight } = drawOptions;
    const width = drawWidth || startWidth;
    const height = drawHeight || startHeight;
    canvas.width = width;
    canvas.height = height;
    let base64Image = '';
    if (ctx) {
        ctx.drawImage(image, startX, startY, startWidth, startHeight, 0, 0, width, height);
        base64Image = canvas.toDataURL(fileType);
        ctx.clearRect(0, 0, width, height);
    }
    return base64Image;
}
export default imageToBase64;
